                "title": "Tasbih in Rukuh-2   (it can be mentioned in sijdah as well)  ",
                "content": "<div class='content'><b class='title'>Tasbih in Rukuh-2  (it can be mentioned in sijdah as well) </b> <ar></ar> <ar></ar> <ar></ar> <ar></ar> <ar></ar> <ar></ar> <ar></ar> <gap></gap><b class='heading'>Pronunciation:</b> Subhanaka Allaahumma Rabbanaa wa bihamdika Allaahum-mag firlee  <gap></gap><b class='heading'>Meaning:</b> Glory to You O Allah!, Our Lord!, And all praises are for You. O Allah! Forgive me.  (Bukhari (I.F. BD) H-780) </div> <div class='words-container'> <b class='title'> Literally Meaning </b> <div class='split-words-container'> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> Glory to You </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> O Allah ! </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> Our Lord </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> and </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> all praises </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> (are) for You </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> O Allah! </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> forgive me </span> </p> </div> </div>"
