                "content": "<div class='content'>There are several Tasbih after standing from ruku in the books of Hadith. However most distinguished and used two of them are given right here.    <b class='title'>Tasbih After Standing From Ruku </b> <ar></ar> <ar></ar> <ar></ar>    <gap></gap><b class='heading'>Pronunciation:</b> Rab-bana lakal hamd  <gap></gap><b class='heading'>Meaning:</b> Oh our Lord, all praises be to you. (Bukhari (I.F. BD) H-2998, 59/The opening chapter of creation, Al-Nas' (I.F BD) H-83) </div> <div class='words-container'> <b class='title'> Literally Meaning </b> <div class='split-words-container'> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> Oh our Lord! </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> (be) to You </span> </p> <p class='split-word'> <span class='split-ar'>  </span> <span class='split-en'> all praises </span> </p> </div> </div>"
