                "content": "<div class='content'><b class='title'>(7)Read more time </b><gap></gap>        Meaning   <ar></ar> <ar></ar>  (Subhana Allah) Glory be to Allah (33 times)  <ar></ar> <ar></ar>  (Al Hamdu Lillah) All praises are due to Allah (33 times) <br/><br/> <ar></ar> <ar></ar>   (Al La Hu Akbar ) Allah is the Greatest (34 times) <br/><br/> in total 100 times. (Abu Dawood H-5082, Mishkat H-2163)   <br/><br/><b class='title'>(8)Read more time </b><br/> Surah Ikhlas, Surah Al-Falaq, Surah An-Naas. ( Please see the Part-2 unit-9 )  Use to recite 3 times after Fajr and Maghrib and 1 time after Zuhr, Asr and Isha. (Abu Dawood H-5082, Mishkat H-2163) </div>"
